@import './../../index.css';

.movie--detail--container{
    background-color: var(--grey);
    font-family: 'Roboto';
    padding: 0.5em 2em;
    color: var(--white);
    width: 60vw;
    margin: 10em auto 6em auto;
    border-radius: 10px;
    position: relative;
}
.movie--detail--teaser{
    width: 100%;
    height: 15em;
}
.movie--detail--firstrow{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2em;
    margin-top: 1em;
}
.movie--detail--title--container{
    display: flex;
    align-items: center;
    gap: 1em;
}
.movie--detail--title{
    border-radius: 15px 15px 0 0;
    font-size: 2em;
    color: var(--white);
    font-weight: bold;
    font-family: 'Oswald';
}
.movie--detail--comingsoon{
    background-color: var(--red);
    text-align: center;
    padding: .5em;
    font-weight: bold;
    font-family: 'Oswald';
}
.movie--detail--button--container{
    opacity: .75;
    transition: .3s;
    background-color: var(--grey);
    padding: .5em;
    border: 2px solid transparent;
    border-radius: 5px;
}
.movie--detail--button--container:hover{
    cursor: pointer;
    opacity: 1;
    border: 2px solid var(--white);
    transition: .3s;
}
.movie--detail--button{
    border-radius: 5px;
    color: var(--white);
    font-weight: bold;
    font-family: 'Oswald';
    display: flex;
    align-items: center;
    gap: 0.5em;
}
/* MODIFICADOR BUTTON "GO BACK" */
.movie--detail--button__goback{
    display: inline-block;
    margin: 1em 0;
}
/* SEGUNDA FILA - MINI INFO */
.movie--detail--rating--container{
    display: flex;
    align-items: center;
    gap: .5em;
    font-family: 'Roboto';
}
.average--rating{
    font-size: 1.5em;
    font-weight: bold;
}
.average--star{
    color: var(--yellow);
}

/* THIRD ROW */
.movie--detail--span{
    font-weight: bold;
}
.movie--detail--info--container{
    margin: 1em 0;
    display: flex;
    gap: 2em;
}
.movie--detail--poster{
    width: 30%;
}
.movie--detail--info{
    display: flex;
    flex-direction: column;
    gap: 1em;
    width: 70%;
    color: var(--white);
}
.movie--detail--description{
    margin: 0;
}
.movie--detail--genre--list, .movie--detail--display--list{
    display: flex;
    align-items: center;
    gap: .5em;
    font-weight: bold;
}
.movie--detail--genre--list{
    gap: .5em;
}
.movie--detail--list--item{
    list-style-type: none;
    background-color: var(--violet-white);
    padding: 0.25em;
    color: var(--grey);
}

/* FOURTH ROW */
.movie--detail--teaser--container{
    margin: 1em 0;
}
.movie--detail--teaser{
    width: 100%;
    /* border: 2px solid var(--violet-white); */
    height: 20em;
}
/* DISEÑO TABLET Y MOBILE */
@media screen and (max-width: 1280px) {
    .movie--detail--container{
        width: 80vw;
        border: none;
    }
}